.contenedor {
  width: 80%;
  max-width: 95rem;
  margin: 0 auto;
}

.portada {
  margin-top: 4rem;
  padding: 0;
  text-align: center;
  height: 48rem;
  background: #7B5ECB;
  background-image: url(/src/img/AllIsometricNov2-10.png);
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 0.8rem;
  position: relative;
}

.titulo {
  position: absolute;
  bottom: 1.2rem;
  left: 1.2rem;
  right: 1.2rem;
  background-color: rgba(43, 6, 85, 0.7);
  padding: 1rem;
  border-radius: 0.5rem;
  color: #FFFFFF;
  text-align: center;
}
.titulo h1 {
  margin: 0;
}
.titulo p {
  margin: 0.5rem 0 0;
  font-size: 2.1rem;
}

.introduccion h2 {
  margin: 2rem 4rem;
}
.introduccion p {
  margin: 1rem 4rem;
  text-align: justify;
}

.contenido h2 {
  text-align: center;
}

.contenedor-temas {
  display: flex;
  justify-content: space-around;
  gap: 1rem;
  text-align: center;
  flex-wrap: wrap;
  margin-top: 2rem;
}
.contenedor-temas .tema {
  padding: 1.5rem;
  border-radius: 0.8rem;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.2);
  text-decoration: none;
  transition: scale 0.3s ease;
}
.contenedor-temas .tema img {
  max-width: 25rem;
  height: 25rem;
  object-fit: cover;
  border-radius: 0.8rem;
}
.contenedor-temas .tema h2 {
  max-width: 25rem;
  font-size: 2.4rem;
  color: #591FB7;
}
.contenedor-temas .tema p {
  max-width: 25rem;
  font-size: 1.8rem;
  color: #591FB7;
}
.contenedor-temas .tema:hover {
  scale: 1.03;
}

.header {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 3.8rem;
  border-bottom: 0.1rem solid #E6DEF2;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.03);
}
.header .logo {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.header h1 {
  font-size: 1.8rem;
  font-weight: 600;
  margin: 0;
}

.nav-bar {
  display: flex;
}
.nav-bar a {
  margin-left: 1rem;
  text-decoration: none;
  color: #1a1b15;
  font-size: 1.5rem;
  font-weight: 400;
  padding: 0.5rem 1rem;
  border-radius: 0.8rem;
  transition: color 0.3s ease, background-color 0.4s ease;
}
.nav-bar a:hover {
  background-color: #591FB7;
  color: #FFFFFF;
}

.footer {
  background-color: #591FB7;
  color: #FFFFFF;
  margin-top: 8rem;
  justify-content: center;
}

.gotop-container {
  position: fixed;
  bottom: 8rem;
  right: 8rem;
}
.gotop-container .oculto {
  display: none;
}
.gotop-container .gotop-btn {
  transition: opacity 0.4s ease;
  padding: 0.5rem;
}
.gotop-container svg {
  transition: transform 0.2s;
}
.gotop-container svg:hover {
  cursor: pointer;
  transform: scale(1.1);
}

